$v_black:#333;
$v_white:#fff;

/*================================================
        
================================================*/
// 
//minmax
//min
//u-mt-10 u-mt-20u-mt-20)
//min (u-mt-10-sm u-mt-smx  u-mt-10-sm)
//max
$v_bp_std: "min";

//min 
//bootstrap
$v_sm: 576;
$v_md: 768;
$v_lg: 992;
$v_xl: 1200;
//max
$v_xlx: 1199;
$v_lgx: 991;
$v_mdx: 767;
$v_smx: 575;

//
$v_bp_min_map: ("sm":$v_sm, "md": $v_md, "lg":$v_lg, "xl":$v_xl,);
$v_bp_max_map: ("xlx":$v_xlx, "lgx":$v_lgx, "mdx":$v_mdx, "smx":$v_smx,);

//
$v_bp_min: map-keys($v_bp_min_map);
$v_bp_max: map-keys($v_bp_max_map);
$v_media_list: ();
@if ($v_bp_std == "min") {
  $v_media_list: join($v_bp_max, $v_bp_min);
} @else if ("max") {
  $v_media_list: join($v_bp_min, $v_bp_max);
}

/*================================================
        area
================================================*/
$v_px: 8;//8px
$v_px_max_roop:5;// 8*50px

$v_percent: 5;//5%
$v_percent_max_roop: (100 / $v_percent);//100%