@mixin u-flex--center {
  -webkit-display: flex;
  -ms-display: flex;
  display: flex;

  -webkit-align-items: center; /* Safari */
  -ms-justify-content: center;
  align-items: center; /*  */

  -webkit-justify-content: center; /* Safari */
  -ms-justify-content: center;
  justify-content: center; /*  */
}

@mixin u-flex--y-center {
  -webkit-display: flex;
  -ms-display: flex;
  display: flex;

  -webkit-align-items: center; /* Safari */
  -ms-justify-content: center;
  align-items: center; /*  */
}

@mixin u-flex--x-center {
  -webkit-display: flex;
  -ms-display: flex;
  display: flex;
  
  -webkit-justify-content: center; /* Safari */
  -ms-justify-content: center;
  justify-content: center; /*  */
}

//@include u-mx-auto;
@mixin u-mx-auto {
  margin-left: auto;
  margin-right: auto;
}
//@include u-my-auto;
@mixin u-my-auto {
  margin-top: auto;
  margin-bottom: auto;
}


