%u-pseudo--center {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  //height
 }
%u-pseudo--y-center {
 content: "";
 position: absolute;
 top: 0;
 bottom: 0;
 margin-top: auto;
 margin-bottom: auto;
 //height
}

%u-pseudo--x-center {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  //height
 }
 
